body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.main-menu {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 3em;
}

.brand {
  display: inline-block;
  padding: 1em 2em;
  cursor: default;
}

.menu-link {
  display: inline-flex;
  padding: 1em 2em;
}

.menu-link a {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}

.shop-cart {
  padding: 0.5em 1em;
  border-radius: 50%;
  border: 2px solid #222;
  margin: auto 1em;
  cursor: pointer;
}
.main-header {
  position: relative;
  margin: 0 auto;
  width: 90%;
  height: 36vh;
  background-image: url(/fashion-today/static/media/landing2.72e71c30.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 25px;
}

.header-text {
  position: absolute;
  width: 36%;
  margin: 8em 0 0 4em;
  padding: 0.2em 0.6em;
  background-color: rgb(185, 182, 182, 0.4);
  border-radius: 15%;
}

.header-text button {
  border-radius: 38%;
  background-color: #ccc;
  padding: 0.3em 0.6em;
  margin: 0 0 1em 3em;
  font-weight: 600;
  font-size: 0.7em;
}
.products-main {
  width: 86%;
  margin: 0 auto;
}

.products-header {
  text-align: center;
  font-size: 26px;
}

.products-wrapper {
  padding-left: 1.3em;
  justify-content: space-between;
  margin: 0 auto;
}

.item {
  width: 16.5vw;
  height: 28vh;
  display: inline-grid;
  padding: 0.5em 0.4em;
  margin: 1.4em 0.4em;
  overflow: inherit;
  transition: all 1s ease-in-out;
}

.item:hover {
  border-radius: 15px;
  border: 2px solid green;
  background-color: rgb(209, 208, 124);
  transition: all .5s ease-in-out;
}

.item-detail {
  margin: 0.1em 0.4em;
  text-align: center;
}

.item-image {
  border: 1px solid red;
  height: 9em;
}


.buy-it {
  display: block;
  margin: 0 auto;
  border-radius: 38%;
  background-color: rgb(152, 219, 182);
  padding: 0.1em 1em;
  font-weight: 600;
  font-size: 1em;
}
